.hero-sec{
        min-height: 100vh;
        background: linear-gradient(120deg, #0a0a23 0%, #1a1a2e 100%);
        color: #f4f4f8;
        display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        position: relative;
        overflow: hidden;
        font-family: 'Poppins', 'Montserrat', sans-serif;
        padding: 20px;
        box-sizing: border-box;
        /* Ensure the hero section remains responsive */
        width: 100%;
        max-width: 1200px; /* Adjust max-width as needed */
        margin: 0 auto; /* Center the content */
      }

.hero-content {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 700px;
  padding: 3rem 1rem 2rem 1rem;
  text-align: center;
  background: rgba(20, 24, 40, 0.65);
  border-radius: 2.5rem;
  box-shadow: 0 8px 48px #00fff733, 0 2px 16px #7f5cff22;
  backdrop-filter: blur(12px);
  border: 1.5px solid rgba(0,255,247,0.08);
  animation: floatCard 5s ease-in-out infinite alternate;
}

.hero-img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 1.2rem;
  border: 4px solid #00fff7;
  box-shadow: 0 0 32px #00fff799;
  animation: floatImg 4s ease-in-out infinite alternate;
}

.hero-title {
  font-family: 'Montserrat', 'Poppins', sans-serif;
  font-size: 3.2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  letter-spacing: 2px;
  text-shadow: 0 4px 32px #000;
  background: linear-gradient(90deg, #00fff7 0%, #ff00cc 60%, #7f5cff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: floatText 3.5s ease-in-out infinite alternate;
}

.hero-subtitle {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: #f4f4f8;
  font-family: 'Poppins', 'Montserrat', sans-serif;
  letter-spacing: 1px;
  text-shadow: 0 2px 12px #00fff744;
  animation: floatText 4.5s ease-in-out infinite alternate;
}

.hero-desc {
  font-size: 1.25rem;
  margin-bottom: 2.5rem;
  color: #b6ffe6;
  text-shadow: 0 2px 8px #000;
  font-family: 'Poppins', 'Montserrat', sans-serif;
  animation: floatText 5.5s ease-in-out infinite alternate;
}

.hero-btn {
  display: inline-block;
  position: relative;
  padding: 1rem 2.8rem;
  font-size: 1.18rem;
  font-weight: 700;
  border-radius: 2rem;
  background: linear-gradient(90deg,#00fff7,#ff00cc 80%, #7f5cff 100%);
  color: #181824;
  text-decoration: none;
  box-shadow: 0 2px 16px #00fff744;
  letter-spacing: 1px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s, background 0.2s;
  margin-bottom: 2.2rem;
  border: none;
  cursor: pointer;
  animation: floatBtn 4s ease-in-out infinite alternate;
}

.divider-animated {
  width: 100%;
  height: 5px;
  margin: 2rem 0 1.5rem 0;
  border-radius: 2px;
  background: linear-gradient(90deg, #00fff7, #ff00cc 60%, #7f5cff 100%);
  box-shadow: 0 2px 16px #00fff744;
  animation: dividerGlow 3s infinite alternate;
}

.hero-badges {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  align-items: center;
  margin-top: 1.2rem;
  flex-wrap: wrap;
}

.badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(0,255,247,0.08);
  color: #fff;
  border-radius: 1.5rem;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 0 2px 8px #00fff722;
  transition: background 0.2s;
}